* {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 14pt;
  margin: 0;
  margin-bottom: 15px;
  padding: 0;
}
.feedButton {
  margin-bottom: 0;
  border-radius: 5px;
  height: 40px;
  width: 100px;
  background-color: dodgerblue;
  overflow: hidden;
}
.feedButton:not(:last-child) {
  margin-right: 15px;
}
.black_yellow {
  border: solid 1px yellow;
  border-radius: 5px;
  color: yellow;
  background-color: black;
  overflow: hidden;
}
section {
  max-width: 350px;
  margin-left: 15px;
  padding: 15px;
  border: 1px solid black;
  border-radius: 5px;
  margin-bottom: 25px;
}
.feedback_form,
h2 {
  text-align: center;
}
p {
  padding: 5px;
}
